<template>
  <div id="app">
    <div id='banner' class='padded'>
      <img src='./assets/SMF_A_plain@3x.png'>
    </div>
    <div id='chart-header'>
      <h1>
        Macro Dashboard  
      </h1> 
    </div> 
    <div v-if="!loading" id='chart-container'>
      
      <div v-for="key in Object.keys(data)" :key="key">
        <LineChart class='chart' v-bind:title='key' v-bind:data='data[key]'></LineChart>
      </div>
    </div>
    </div>
  </div>
</template>

<script>
import LineChart from "./components/LineChart.vue";
import axios from 'axios';
import * as d3 from "d3";

export default {
  name: "App",
  components: {
    LineChart
  },
  data() {
    return {
      data: {},
      loading: true,
    }
  },
  created() {
        const path = 'http://localhost:5000/chart_data';

        axios.get(path)
            .then((res) => {
                console.log("Recieved data...")
                this.data = res.data

                var parseDate = d3.timeParse("%Y-%m-%d");

                Object.keys(this.data).forEach((key) => {
                  this.data[key].forEach(function(d) {
                    d.date = parseDate(d.date);
                  });
                });
                
                console.log("Recieved data...")
                this.loading = false
            })
            .catch((error) => {
                // eslint-disable-next-line
                console.error(error);
            }); 
    },
};
</script>

